import React from 'react';
import classes from "./ChatInputButtons.module.css";

const ChatInputButtons = (props) => {

    return (
        <div className={classes.ButtonsContainer}>
            {
                props.buttons.map(buttonData => (
                    <button
                        className={classes.Button}
                        key={buttonData.description}
                        onClick={() => props.clickButtonHandler(buttonData)}
                    >
                        {buttonData.description}
                    </button>
                ))
            }
        </div>
    )
}

export default ChatInputButtons;
