import React, {useEffect, useState} from 'react';
import { Map, TileLayer, Popup, Circle } from "react-leaflet";
import Spinner from "../Spinner/Spinner";
//import {serverIp} from "../../utils/settings";

const mapStyle = {
    width: "90%",
    maxWidth: "700px",
    height: "65vh",
    borderRadius: "5px",
    boxShadow: "0 0 5px rgba(0,0,0,.5)",
    margin: "10px auto 20px auto",
    zIndex: "0",
};
const loadingMapStyle = {
    ...mapStyle,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "white"
}

const LeafletMap = () => {
    const position = [-38.012, -57.587]; // ([0.0025, -0.0025], [0.0045, -0.0045])
    const [locations, setLocations] = useState([]);

    const loadLocations = async () => {
        try{
            const data = await fetch(`data/locations`, {
                method: "GET"
            });
            const {locations} = await data.json();
            
            setLocations(locations);

        }catch(err){
            console.log(err);
        }
    }

    useEffect(() => {
        loadLocations();
    }, []);

    return (
        (locations.length <= 0)
        ? <div style={loadingMapStyle}>
            <Spinner />
        </div>
        : <Map center={position} zoom={4} style={mapStyle}>
            <TileLayer
            attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            />
            {
                locations.map(location => (
                    <Circle
                        key={location._id}
                        center={[location.latitude, location.longitude]}
                        radius={400}
                        color="#F000"
                        fillColor="red"
                        fillOpacity={0.3}
                    >
                        <Popup>
                            Resultado del test: {location.finalResult}%
                        </Popup>
                    </Circle>
                ))
            }
        </Map>
    );
};

export default LeafletMap;
