import React from 'react';
import classes from "./ChatDescription.module.css";
import CobitHeader from "../../components/CobitHeader/CobitHeader";
import ButtonLink from "../../components/ButtonLink/ButtonLink";

const Description = () => {
    return (
        <div className={classes.ChatDescription}>
            <CobitHeader />
            <section>
                <p>
                    Este es un ChatBot en desarrollo, si quiere información mas detallada 
                    haga <a href="https://www.who.int/es/emergencies/diseases/novel-coronavirus-2019" target="_blank" rel="noopener noreferrer">Click aquí</a>
                </p>
                <ButtonLink to="/map">
                    Ver mapa de tests hechos por CoBit
                </ButtonLink>
            </section>
        </div>
    );
};

export default Description;
