import React from 'react';
import {ReactComponent as LogoSvg} from "../../assets/Logo_CovidBot-19_opt.svg";
import styled, {keyframes} from "styled-components";

const shakeHeart = keyframes`
    0%{
        transform: translateY(0)
    }
    20%{
        transform: translateY(6%)
    }
    40%{
        transform: translateY(-4%)
    }
    60%{
        transform: translateY(3%)
    }
    80%{
        transform: translateY(-2%)
    }
    100%{
        transform: translateY(0)
    }
`;
const shakeFace = keyframes`
    0%{
        transform: translateY(0%);
    }
    50%{
        transform: translateY(4%);
    }
    100%{
        transform: translateY(0%);
    }
`;

const StyledSvg = styled.div`
    width: 100%;
    height: 100%;

    #gHeart{        
        transition: fill .3s;
    }
    svg:hover > #gHeart{
        /*transform: rotateZ(360deg);*/
        fill: red;
        animation: ${shakeHeart};
        animation-duration: .5s;
        animation-fill-mode: forwards;
    }
    svg:hover #outerHead,
    svg:hover #innerHead,
    svg:hover #face{
        animation: ${shakeFace} .5s ease forwards; 
    }
`

const LogoSVG = () => {
    return (
        <StyledSvg>
            <LogoSvg />
        </StyledSvg>
    );
};

export default LogoSVG;
