import React, {useRef} from 'react';
import classes from  './MainPage.module.css';
import ChatInterface from "../../containers/ChatInterface/ChatInterface";
import ChatDescription from "../../containers/ChatDescription/ChatDescription";
import {
  IoIosArrowForward as IoArrowForward, 
  IoIosArrowBack as IoArrowBack
} from "react-icons/io";

const MainPage = () => {
  const appRef = useRef();

  const goToChatHandler = (going) => {
    if(going){
      appRef.current.scrollLeft = appRef.current.scrollWidth;
    }
    else{
      appRef.current.scrollLeft = 0;
    }
  }

  return (
    <div className={classes.MainPage} ref={appRef}>
      <div className={classes.ButtonNavigator}>
        <button onClick={() => goToChatHandler(true)}>
          <p>Ir al Chat</p>
          <IoArrowForward />
        </button>
        <button onClick={() => goToChatHandler(false)}>
          <IoArrowBack />
          <p>Ir a la descripción</p>
        </button>
      </div>
      <div className={classes.ChatContainer}>
        <ChatInterface />
        <ChatDescription />
      </div>
    </div>
  );
}

export default MainPage;