import React from 'react';
import ButtonLink from "../../components/ButtonLink/ButtonLink";
import classes from "./MapPage.module.css";
import CobitHeader from "../../components/CobitHeader/CobitHeader";
import LeafletMap from "../../components/LeafletMap/LeafletMap";

const MapPage = () => {
    return (
        <div className={classes.MapPage}>
            <CobitHeader subtitle={"Mapa de los test realizados"}/>
            <LeafletMap />
            <ButtonLink to="/">Volver a la pagina principal</ButtonLink>
            <span>Las ubicaciones presentadas en el mapa son tests realizados por CoBit, no deben tomarse como casos reales.</span>
            <span>Las ubicaciones no son exactas, con el objetivo de proteger la privacidad del usuario.</span>
        </div>
    );
};

export default MapPage;
