import React, {useState, useRef, useEffect} from 'react';
import classes from "./ChatInterface.module.css";
import DialogueBubble from "./DialogueBubble/DialogueBubble";
import ChatInput from "./ChatInput/ChatInput";
import ChatInputButtons from "./ChatInputButtons/ChatInputButtons";
import SocketIOClient from "socket.io-client";
// import {serverIp} from "../../utils/settings";

const ChatInterface = () => {
    const [userMessage, setUserMessage] = useState("");
    const [messages, setMessages] = useState([{
        speaker: "CHATBOT", 
        text: "ChatBot: Hola soy un chatbot con información sobre la Covid-19, preguntame lo que quieras saber sobre el virus."
    }]);
    const dialogBoxRef = useRef();
    const socket = useRef();
    const [testData, setTestData] = useState(null);
    const [locationId, setLocationId] = useState(null);

    const onTypingHandler = (e) => {
        setUserMessage(e.target.value);
    }
    const sendMessageHandler = () => {
        if(userMessage.length > 0){
            socket.current.emit("chat:message", {
                message: userMessage
            });

            setMessages(prevState => [...prevState, {
                speaker: "USER",
                text: `Yo: ${userMessage}`
            }])
            setUserMessage("");
        }
    }

    const askForLocationHandler = (answer) => {
        const id = locationId;
        if(answer.value){
            navigator.geolocation.getCurrentPosition((position) => {
                console.log(position.coords.latitude, position.coords.longitude);
                socket.current.emit("chat:location", {
                    id,
                    lat: position.coords.latitude,
                    long: position.coords.longitude
                });
            });
            setMessages(prevState => [...prevState, {
                speaker: "CHATBOT",
                text: `Chatbot: Bien, gracias. \nContinúe haciéndome preguntas si lo necesita.`
            }]);
        }
        else{
            setMessages(prevState => [...prevState, {
                speaker: "CHATBOT",
                text: `Chatbot: Bien, no hay problema. \nContinúe haciéndome preguntas si lo necesita.`
            }]);
        }
        setLocationId(null);
    }

    const clickButtonTestHandler = (answer) => {
        setTestData({options: []});
        setMessages(prevState => [...prevState, {
            speaker: "USER",
            text: `Yo: ${answer.description}`
        }])
        socket.current.emit("chat:test", {
            answer: answer.value
        });
    }

    useEffect(() => {
        socket.current = SocketIOClient();

        socket.current.on("chat:message", (data) => {
            data.messages.forEach(message => {
                setMessages(prevState => [...prevState, {
                    speaker: "CHATBOT",
                    text: `Chatbot: ${message}`
                }]);
            });
        });

        socket.current.on("chat:test", (data) => {
            if(data.testEnd){
                const chatbotMessages = [{
                    speaker: "CHATBOT",
                    text: `Chatbot: El test termino. Estamos procesando los datos...`
                }, {
                    speaker: "CHATBOT",
                    text: `Chatbot: Usted tiene un ${data.covidPercentage}% de probabilades de haber contraido Covid-19.`
                }];
                if(data.messages){
                    data.messages.forEach(message => {
                        chatbotMessages.push({
                            speaker: "CHATBOT",
                            text: message
                        })
                    })
                }

                setTestData(null);
                setMessages(prevState => [...prevState, ...chatbotMessages]);
            }else {
                setTestData(data);
                setMessages(prevState => [...prevState, {
                    speaker: "CHATBOT",
                    text: `Chatbot: ${data.question}`
                }]);
            }
        });

        socket.current.on("chat:location", (data) => {
            if ("geolocation" in navigator) {
                setMessages(prevState => [...prevState, {
                    speaker: "CHATBOT",
                    text: `Chatbot: Nos permitiría guardar su localización? Es solo para estadísticas, la información es anónima.`
                }]);
                setLocationId(data._id);
            }
        })
    }, []);

    useEffect(() => {
        dialogBoxRef.current.scrollTop = dialogBoxRef.current.scrollHeight;
    }, [dialogBoxRef, messages]);



    let ChatInputComponent = <ChatInput 
        value={userMessage}
        onTyping={onTypingHandler}
        onPressButton={sendMessageHandler}
    />
    if(testData){
        ChatInputComponent = <ChatInputButtons 
            buttons={testData.options} 
            clickButtonHandler = {clickButtonTestHandler}
        />
    }else if(locationId){
        ChatInputComponent = <ChatInputButtons
            buttons = {[{
                description: "Si",
                value: true
            }, {
                description: "No",
                value: false
            }]}
            clickButtonHandler = {askForLocationHandler}
        />
    }

    return (
        <div className={classes.ChatInterface}>
            <div className={classes.DialogBox} ref={dialogBoxRef}>
                {
                    messages.map((message, index) => (
                        <DialogueBubble key={index} speaker={message.speaker}>
                            {message.text}
                        </DialogueBubble>
                    ))
                }
            </div>
            {ChatInputComponent}
        </div>
    );
};

export default ChatInterface;
