import React from 'react';
import classes from "./DialogueBubble.module.css";

const DialogueBubble = (props) => {

    const BubbleClasses = [classes.DialogueBubble];
    if(props.speaker === "USER"){
        BubbleClasses.push(classes.UserBubble);
    }
    else if(props.speaker === "CHATBOT" ){
        BubbleClasses.push(classes.ChatbotBubble);
    }

    return (
        <div className={classes.BubbleContainer}>
            <p className={BubbleClasses.join(" ")}>
                {props.children}
            </p>
        </div>
    )
}

export default DialogueBubble
